
.slide {
  width: 100%;
  position: relative;
  overflow: hidden;
}
.slide:after {
  content: '';
  display: block;
  padding-top: 60%;
}
.slide .slide-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background: #f2f2f2;
}
.slide .slide-wrapper .slide-box {
  height: 100%;
  position: absolute;
  top: 0;
  -webkit-transition-property: all !important;
  transition-property: all !important;
}
.slide .slide-wrapper .slide-box .slide-item {
  height: 100%;
  float: left;
  background-size: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  position: relative;
}
.slide .slide-wrapper .slide-box .slide-item .slide-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: gradient(linear, left top, left bottom, color-stop(25%, rgba(0,0,0,0)), color-stop(100%, rgba(0,0,0,0.6)));
  background: -webkit-gradient(linear, left top, left bottom, color-stop(25%, rgba(0,0,0,0)), to(rgba(0,0,0,0.6)));
  background: linear-gradient(to bottom, rgba(0,0,0,0) 25%, rgba(0,0,0,0.6) 100%);
}
.slide .slide-wrapper .slide-box .slide-item .slide-title {
  position: absolute;
  left: 0;
  bottom: 15px;
  font-weight: 500;
  padding: 0 20px;
  margin: 0;
  font-size: 20px;
  line-height: 1.2;
  color: #fff;
}
.slide .slide-wrapper .slide-pagination {
  width: 100%;
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 6px;
}
.slide .slide-wrapper .slide-pagination .dot {
  width: 8px;
  height: 8px;
  margin: 0 5px;
  display: inline-block;
  border-radius: 50%;
  background: #5a5a5a;
  opacity: 0.8;
}
.slide .slide-wrapper .slide-pagination .dot.active {
  opacity: 1;
  background: #fff;
}


