
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.fade-enter-active .sidebar-mask,
.fade-leave-active .sidebar-mask {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.fade-leave-to,
.fade-enter {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.fade-leave-to .sidebar-mask,
.fade-enter .sidebar-mask {
  opacity: 0 !important;
}
