
.homepage {
  left: 0;
}
.homepage .homepage-content {
  position: relative;
  top: 1.333rem;
}
.homepage .homepage-content .loading-wrapper {
  background: #f2f2f2;
}
.homepage .change-mode {
  position: fixed;
  z-index: 150;
  top: 3px;
  right: 3px;
  width: 50%;
  height: 1.633rem;
  -webkit-box-shadow: -5px 10px 20px rgba(0,0,0,0.5);
          box-shadow: -5px 10px 20px rgba(0,0,0,0.5);
  border-radius: 3px;
  background: #fff;
}
.homepage .change-mode .text {
  position: relative;
  z-index: 120;
  padding-left: 15px;
  font-size: 16px;
  line-height: 1.633rem;
  color: #2c3e50;
}
.homepage .change-mode .mode-mask {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}


