
@import "../../common/stylus/share.css";
.slide-enter-active,
.slide-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  position: fixed !important;
}
.slide-enter,
.slide-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.stories-detail {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 200;
  top: 0;
  left: 0;
  background: #fff;
}
.stories-detail .header {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 1.333rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  padding-right: 0.267rem;
  background: #00a2ea;
  color: #fff;
}
.stories-detail .header .header-blank {
  -webkit-box-flex: 1.6;
      -ms-flex: 1.6;
          flex: 1.6;
}
.stories-detail .header .header-icon {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  font-size: 0;
}
.stories-detail .header .header-icon .icon-font {
  font-size: 0.507rem;
  line-height: 1.413rem;
  background: none;
}
.stories-detail .header .header-icon .icon-font.message {
  position: relative;
  top: 0.03rem;
}
.stories-detail .header .header-icon .icon-font.highlight {
  color: #ffa500;
}
.stories-detail .header .header-icon .icon-font.favorite {
  color: #ff0;
}
.stories-detail .header .header-icon .count {
  font-size: 0.373rem;
  margin-left: 0.08rem;
  vertical-align: 0.03rem;
}
.stories-detail .main {
  position: relative;
  top: 1.333rem;
}
.stories-detail .main .img-box {
  width: 100%;
  position: relative;
  overflow: hidden;
  background-size: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}
.stories-detail .main .img-box:after {
  content: '';
  display: block;
  padding-top: 60%;
}
.stories-detail .main .img-box .img-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: gradient(linear, left top, left bottom, color-stop(25%, rgba(0,0,0,0)), color-stop(100%, rgba(0,0,0,0.6)));
  background: -webkit-gradient(linear, left top, left bottom, color-stop(25%, rgba(0,0,0,0)), to(rgba(0,0,0,0.6)));
  background: linear-gradient(to bottom, rgba(0,0,0,0) 25%, rgba(0,0,0,0.6) 100%);
}
.stories-detail .main .img-box .img-title {
  position: absolute;
  left: 0;
  bottom: 15px;
  font-weight: 500;
  padding: 0 20px;
  margin: 0;
  font-size: 20px;
  line-height: 1.2;
  color: #fff;
}
.stories-detail .main .img-box .img-source {
  position: absolute;
  bottom: 10px;
  right: 0;
  padding: 0 20px;
  font-weight: 300;
  font-size: 12px;
  color: #d3d3d3;
}
.stories-detail .loading-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
}


